

























































































import { Vue, Component } from "vue-property-decorator";
import VuetifyLayout from "./components/VuetifyLayout.vue";

@Component({
  components: {
    VuetifyLayout
  }
})
export default class VuetifyMessage extends Vue {
  private handleVNodeMessage() {
    this.$message.success(this.$createElement("div", "Message使用VNode"));
  }

  private handleVNodeCloaseButton() {
    this.$message
      .closeButtonContent(this.$createElement("div", "VNode Close"))
      .success("关闭按钮使用VNode");
  }

  private handleCustom() {
    this.$message
      .topRight()
      .height("1000px")
      .centered()
      .save("custom_bar");
    this.$message.read("custom_bar").success("abc");
    this.$nextTick(() => {
      this.$message.read("custom_bar").info("cde");
    });
  }

  private handleClose() {
    const bar = this.$message
      .top()
      .success("这个消息条将在1秒钟后通过close方法关闭");

    setTimeout(() => {
      bar.close();
    }, 1000);
  }

  private handleAgain() {
    const bar = this.$message
      .top()
      .timeout(-1)
      .closeButtonContent("^-^")
      .info("消息条关闭1秒后将会通过again方法创建一个相同的消息条");

    setTimeout(() => {
      bar.close();

      setTimeout(() => {
        bar.again();
      }, 1000);
    }, 2000);
  }
}
